<template>
    <div style="text-align: center; ">
        <h2>오늘식수</h2>
    </div>
    <div>
        <div v-for="user in this.users" :key="user.uid"> {{ user.username }}</div>        
    </div>    
    <div> 전체식수 : {{ this.ticketLength }} 명</div>
    <b-button to="/home" size="sm" variant="success">홈</b-button>
    <!-- <div v-for="user in this.users" :key="user.id"> {{ user }}</div>         -->
    
</template>

<script>
import {ref} from 'vue';
const in_ticketLength = ref('0');
const in_users=ref([]);

export default {
    name: "TicketCount",
    components: { 
        // Header
    },
    data() {
        return{
            ticketLength:in_ticketLength,
            users:in_users,
        }        
    },
    mounted() {
        this.$store.dispatch("fetchAllTicketList");
        this.$store.dispatch("fetchAlluserList");
    },
    watch:{
        getAllTicketList(val){
            if ( val.length > 0) {
                let usersArray=[];
                this.ticketLength = val.length;
                let users = this.$store.state.auth.userList;                        
                // console.log('users', users);
                val.forEach( ticket =>{
                    var hasname = e => e.uid === ticket.uid;
                    let uname= users.find(hasname);
                    usersArray.push(uname);
                    // console.log('ddd', uname.username);                   
                });
                this.users = usersArray;
                // console.log(this.users);
            }
        },
        getAlluserList(val) {
            this.userList = val;            
        }
    },
    computed: {
        getAllTicketList() {        
            return this.$store.state.tickets.allticketList;            
        },
        getAlluserList() {
            return this.$store.state.auth.userList;
        }
    }
}
</script>